.about-entry-title {
  position: absolute;
  background-color: none;
  color: white; }

.entry-title {
  position: absolute;
  top: 20px;
  left: 20;
  font-family: "Novecento Sans Web, sans-serif"; }

.entry-header, .entry-title {
  background-color: green;
  color: white; }

.menu .menu-item {
  color: white; }

.entry-meta {
  position: absolute;
  right: 0;
  bottom: 0;
  background-color: green;
  color: white;
  font-size: 1rem;
  font-family: "Novecento Sans Web, sans-serif"; }

.entry-content p {
  max-width: 750px; }

.about-entry-content {
  width: 100%;
  height: 100vh; }

/*--------------------------------------------------------------
# Front Page
--------------------------------------------------------------*/
.site-header .banner-tent, .site-header img, .site-branding .banner-tent, .site-branding img {
  width: 65px;
  position: fixed; }

/*--------------------------------------------------------------
# Find Us
--------------------------------------------------------------*/
.hero-image-header {
  height: 100vh; }

.page-about .entry-title {
  display: none; }

.page-about .about-entry-content {
  display: flex;
  flex-direction: column; }

.page-about h1.about-entry-title {
  justify-content: center;
  align-items: center; }

.single-product, article, aside, details, figcaption, figure, header, main, section, summary {
  /* display: block; */
  display: flex; }

.single-product .entry-content {
  max-width: 450px;
  margin: 0 auto;
  padding-left: 25px; }

.entry-header {
  padding-left: 25px; }

.single-product .product-info {
  padding-top: 40px;
  height: 400px;
  margin: 0 auto; }

.single-product .hentry .entry-title {
  color: #d99054;
  font-family: Novecento Sans Web, sans-serif;
  letter-spacing: 2px;
  text-transform: uppercase;
  position: relative;
  top: 0px; }

.single-product, .entry-header, .entry-title {
  background-color: white; }
